body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App { 
  height: 100%;
  padding-top: 10px;
  background-color: #1b1b32;
  font-family: Lato, sans-serif;
  display:flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.freecodecamp-logo-container {
  width: 100%;
  height: 50px;
  margin: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.freecodecamp-logo {
  height: 40px;

}

.calculator-container {
  width: 400px;
  height: 600px;
  padding: 14px;
  background-color: #99c9ff;
  border-radius: 20px;
  border: 5px solid white;
}
.row {
  margin: 4px;
  display:flex;
  align-items: center;
  justify-content: center;
}

.Containeer-Button1 {
    height: 83px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 38px;
    background-color: #1b1b32;
    color: white;
    border-radius: 25px;
    border: 2px solid white;
    margin: 4px;
    cursor: pointer;
    -webkit-user-select: none;
            user-select: none;
    flex: 1 1;
}

.Containeer-Button1:hover {
    background-color: #5a01a7;
}

.operator {
    background:#00471b;
}

.operator:hover {
    background-color: #ff6600;
}
.input {
    height: 75px;
    border-radius: 50px;
    margin-bottom: 20px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    font-weight: bold;
    font-size: 30px;
    background-color: #0a0a23;
    color:white;
    padding: 11px 30px 11px 11px;
    margin: 1px solid #888;
    box-shadow: -1px 4px 1px 1px white;
}
.button-clear {
    height: 80px;
    font-size: 1.6em;
    display: flex;
    flex: 1 1;
    background-color: #ac0246;
    margin-top: 8px;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    color: white;
    border: 2px solid white;
    cursor: pointer;
    border-radius: 45px;
}

button-clear:hover {
    background-color: #ebb000;
}
